.listHeader{@apply mb-[25px] flex w-fit gap-[15px] bg-[#B7D4FF] px-[28.5px] py-[12px] max-md:gap-[10px] max-sm:mb-0;border-radius:20px 0px}@media not all and (min-width: 650px){.listHeader{border-radius:10px 0px}}.listHeader .label{@apply text-[18px] font-medium leading-[21px] text-[#4674B8] max-sm:text-[14px] max-sm:leading-[16px]}.listHeader .label:where(:global(.dark),:global(.dark) *){@apply text-[#9B9B9B]}.listHeader:where(:global(.dark),:global(.dark) *){@apply bg-[#585E67]}