.backdrop{@apply fixed left-0 top-0 z-50 flex h-[100vh] w-full items-center justify-center bg-[#B7D4FF1A] pl-[120px] pr-[20px] max-sm:px-[15px];backdrop-filter:blur(2.5px)}.backdrop:where(:global(.dark),:global(.dark) *){@apply bg-[#0000001A]}.modal{@apply min-w-0 max-w-[714px] flex-grow rounded-[20px] bg-white px-[95px] py-[60px] max-sm:px-[20px];box-shadow:0px 4px 25px 0px rgba(70,116,184,.2)}.modal .title{@apply mb-[40px] text-center text-[24px] font-medium leading-[28px] text-[#171717] max-sm:text-[20px]}.modal .description{@apply mb-[35px] text-center text-[15px] leading-[18px] text-[#838383]}.modal:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31];box-shadow:0px 4px 25px 0px rgba(0,0,0,.2)}.modal:where(:global(.dark),:global(.dark) *) .title{@apply text-[#F0F5FC]}.modal:where(:global(.dark),:global(.dark) *) .description{@apply text-[#8B8B8D]}