.body{@apply h-full min-h-0 w-full rounded-[8px] border border-[#F1F1FA] pl-[15px] pr-[10px] max-sm:rounded-[12px] max-sm:border-[#DADAEA];grid-column:span 3;display:grid;grid-template-columns:subgrid}.body .scrollContainer{@apply relative h-full min-h-0 overflow-auto overflow-x-hidden;grid-column:span 3;display:grid;grid-template-columns:subgrid;grid-auto-rows:64px}.body .scrollContainer .empty{@apply absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 text-[14px] font-medium text-[#6a6a6a];white-space:nowrap}.body .scrollContainer::-webkit-scrollbar{width:4px;height:0px}@media not all and (min-width: 650px){.body .scrollContainer{grid-auto-rows:40px}}.body:where(:global(.dark),:global(.dark) *){@apply border-[#242527]}