.container{@apply flex w-fit cursor-pointer items-center gap-[10px]}.container .label{@apply text-[13px] font-normal text-[#838383]}.container .switch{@apply h-[15px] w-[35px] rounded-[20px] bg-[#EEEEF8]}.container .switch .core{@apply relative top-1/2 h-[21px] w-[21px] -translate-y-1/2 rounded-full bg-[#FFFFFF] transition-all ease-linear;box-shadow:0px 0px 10px 0px rgba(70,116,184,.25)}.container .switch:hover .core{@apply bg-[#B7D4FF];box-shadow:0px 0px 10px 0px rgba(70,116,184,.25)}.container .switch:where(:global(.dark),:global(.dark) *){@apply bg-[#222222]}.container .switch:where(:global(.dark),:global(.dark) *) .core{@apply bg-[#6F6F6F];box-shadow:0px 0px 10px 0px rgba(70,116,184,.25)}.container .switch:where(:global(.dark),:global(.dark) *):hover .core{@apply bg-[#B7D4FF];box-shadow:0px 0px 10px 0px rgba(70,116,184,.25)}.checked .switch .core{@apply translate-x-[14px] bg-[#4674B8];box-shadow:0px 0px 10px 0px rgba(70,116,184,.25)}