.multipleActions{@apply sticky bottom-[10px] flex h-0 flex-shrink-0 items-center justify-center gap-[20px] overflow-hidden max-lg:gap-[10px];transition-property:height;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-duration:150ms}.multipleActions .button{@apply h-[43px] w-[170px] rounded-[10px] bg-white max-lg:h-[40px] max-lg:w-[140px]}.multipleActions .button:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}