.gridContainer{width:100%;display:grid;grid-template-columns:repeat(var(--col-count), 1fr);grid-gap:15px;grid-auto-rows:78.5px;justify-items:center;max-height:270px;overflow:auto}@media not all and (min-width: 820px){.gridContainer{grid-template-columns:repeat(calc(var(--col-count) - 1), 1fr)}}@media not all and (min-width: 650px){.gridContainer{grid-auto-rows:150px;max-height:fit-content;overflow:visible}}