.label{@apply mb-[6px] text-[13px] text-input-label}.input{@apply mb-[5px] h-[40px] w-full rounded-[8px] border bg-transparent px-[30px] py-[11px] text-[15px] font-normal outline-none placeholder:text-[#B1B1B1] max-sm:h-[35px]}.input:where(:global(.dark),:global(.dark) *){@apply placeholder:text-[#6A6A6A]}.error_on{@apply border-input-failure text-input-failure}.error_off{@apply border-input-idle text-input-main focus:border-input-main}.error_off:where(:global(.dark),:global(.dark) *){@apply border-input-idleDark}.listValues{@apply flex flex-wrap items-center gap-[5px]}.listValues .value{@apply flex items-center gap-[5px]}.listValues .value span{@apply rounded-[4px] bg-[#B7D4FF] px-[8px] py-[2px] text-[16px] text-[#4674B8]}.listValues .value span:where(:global(.dark),:global(.dark) *){@apply bg-[#8297B5] text-[#ffffff]}.errorMessage{@apply mt-[5px] text-[13px] font-normal leading-[15px] text-input-failure}