.label{@apply mb-[5px] text-[13px] leading-[15px] text-[#838383]}.container{@apply flex h-[40px] cursor-pointer items-center rounded-[8px] border border-[#B1B1B1] px-[20px]}.container .input{@apply w-full cursor-pointer bg-transparent text-[15px] font-normal text-[#4674B8] outline-none placeholder:text-[#B1B1B1]}.container:where(:global(.dark),:global(.dark) *){@apply border-[#6A6A6A]}.listValues{@apply absolute z-[80] w-full flex-col gap-[5px] overflow-auto rounded-[8px] border border-[#9EB9E0] bg-[#F0F5FC] p-[10px];bottom:calc(100% + 10px);box-shadow:0px 0px 40px 0px rgba(70,116,184,.15)}.listValues:where(:global(.dark),:global(.dark) *){@apply border-[#585E67] bg-[#585E67];box-shadow:0px 0px 40px 0px rgba(0,0,0,.2)}.errorMessage{@apply mt-[5px] text-[13px] font-normal leading-[15px] text-input-failure}