.container{@apply w-[30%] rounded-[20px] bg-white p-[30px] max-sm:rounded-[6px]}.container .name{@apply mb-[20px]}.container .switch{@apply mb-[20px] flex justify-between}.container .switch .label{@apply text-[14px] text-[#838383]}@media not all and (min-width: 1100px){.container .switch{@apply max-w-[300px]}}.container .labelGroup{@apply mb-[10px] text-[16px] font-semibold text-[#171717]}.container .labelGroup:where(:global(.dark),:global(.dark) *){@apply text-[#9B9B9B]}.container .radioGroup{@apply flex flex-wrap gap-[20px]}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1200px){.container{@apply p-[20px] max-sm:px-[15px]}}@media not all and (min-width: 1100px){.container{@apply w-full}}