.container{@apply flex h-[190px] w-full flex-col rounded-[20px] bg-white max-lg:h-fit max-sm:rounded-[6px]}.container .label{@apply w-fit bg-[#B7D4FF] px-[30px] py-[10px] text-[15px] font-medium text-[#4674B8] max-sm:mx-auto max-sm:mt-[40px] max-sm:border max-sm:border-[#B7D4FF] max-sm:bg-inherit max-sm:px-[15px] max-sm:py-[4px] max-sm:text-[11px];border-radius:20px 0px}.container .label:where(:global(.dark),:global(.dark) *){@apply bg-[#585E67] text-[#C5C5C5] max-sm:border-[#4674B8] max-sm:text-[#B7D4FF]}@media not all and (min-width: 650px){.container .label{@apply bg-transparent;border-radius:60px}}.container .content{@apply flex h-full items-center justify-evenly px-[15px] pb-[35px] pt-[13px] max-lg:flex-col max-sm:pb-[40px]}.container .content .balance{@apply text-[28px] font-bold text-[#4674B8] max-lg:mb-[30px] max-lg:text-[36px]}.container .content .mobileDivider{@apply mx-auto mb-[30px] hidden h-[1px] w-full flex-shrink-0 bg-[#F2F2FE] max-lg:block;width:calc(100% - 100px)}.container .content .mobileDivider:where(:global(.dark),:global(.dark) *){@apply bg-[#252628]}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}