.container{@apply w-full}.container .label{@apply mb-[6px] text-[13px] text-input-label}.container .textField{@apply relative flex w-full items-center rounded}.container .textField .input{@apply w-full resize-y rounded-[8px] border border-[#B1B1B1] bg-transparent px-[30px] py-[11px] text-[15px] font-normal outline-none placeholder:text-[#B1B1B1]}.container .textField .input:focus~.clearButton{@apply text-input-main}.container .textField .input:where(:global(.dark),:global(.dark) *){@apply placeholder:text-[#6A6A6A]}.container .textField .clearButton{@apply absolute right-[12px] text-input-idle}.container .textField .clearButton .icon{@apply w-[14px] cursor-pointer opacity-60 transition-opacity ease-linear}.container .textField .clearButton .icon:hover{@apply opacity-100}.container .textField .clearButton:where(:global(.dark),:global(.dark) *){@apply text-input-idleDark}.container .error_on .input{@apply border-input-failure text-input-failure}.container .error_on .clearButton .icon{@apply text-input-failure}.container .error_off .input{@apply border-input-idle text-input-main focus:border-input-main}.container .error_off .input:where(:global(.dark),:global(.dark) *){@apply border-input-idleDark}.container .error_off .clearButton .icon{@apply text-inherit}.container .errorMessage{@apply mt-[5px] text-[13px] font-normal leading-[15px] text-input-failure}