.list{@apply flex min-w-0 flex-col gap-[20px] overflow-auto pr-[15px] max-sm:overflow-visible max-sm:pr-0;height:calc(100% - 68px)}.centered{@apply flex h-full w-full items-center justify-center;grid-column:1/-1}.centered .message{@apply text-center text-[22px] font-medium text-[#6A6A6A] max-sm:text-[18px]}