.layout{@apply h-[100vh] w-full min-w-0 p-[25px] pl-[5px] max-sm:px-[10px] max-sm:pb-[20px] max-sm:pt-0}.layout .scrollContainer{@apply flex w-full flex-col gap-[20px] overflow-auto pr-[5px];height:calc(100% - 15px - 43px - 25px)}.layout .scrollContainer .flexContainer{@apply flex min-h-full w-full gap-[20px]}@media not all and (min-width: 1100px){.layout .scrollContainer .flexContainer{@apply relative max-h-fit flex-col gap-[10px]}}@media not all and (min-width: 1100px){.layout .scrollContainer{@apply gap-[10px]}}@media not all and (min-width: 1100px){.layout{@apply h-fit}}