.pageButton{@apply flex h-[30px] flex-grow items-center justify-center rounded-[5px] border border-[#B7D4FF] px-[15px] transition-colors ease-linear max-lg:h-[23px] max-lg:px-[11.5px]}.pageButton .value{@apply text-[15px] font-medium text-[#4674B8] transition-colors ease-linear max-lg:text-[11px]}.pageButton:hover{@apply border-[#6491D3] bg-[#6491D3]}.pageButton:hover .value{@apply text-[#ffffff]}.pageButton_active{@apply border-[#4674B8] bg-[#4674B8]}.pageButton_active .value{@apply text-[#ffffff]}.pageButton_active:where(:global(.dark),:global(.dark) *){@apply border-[#4674B8]}.pageButton:where(:global(.dark),:global(.dark) *){@apply border-[#4674B8]}@media not all and (min-width: 1110px){.pageButton:nth-child(2):not(.pageButton_active),.pageButton:nth-child(3):not(.pageButton_active),.pageButton:nth-child(4):not(.pageButton_active),.pageButton:nth-child(5):not(.pageButton_active),.pageButton:nth-child(6):not(.pageButton_active){@apply hidden}.pageButton:last-child:not(.pageButton_active){@apply flex}}