.layout{@apply flex h-[100vh] w-full flex-col p-[25px] pr-[10px] max-sm:h-fit}.layout .scrollContainer{@apply h-full w-full overflow-auto pr-[15px] max-sm:pr-0}.layout .scrollContainer .flexContainer{@apply mb-[20px] flex w-full grow gap-[20px] max-sm:mb-[10px] max-sm:gap-[10px]}@media not all and (min-width: 910px){.layout .scrollContainer .flexContainer{@apply flex-col}}@media not all and (min-width: 910px){.layout{@apply pl-0}}@media not all and (min-width: 650px){.layout{@apply px-[10px] pt-0}.layout .scrollContainer{max-height:calc(100vh - 75px - 55px - 25px)}}