.scrollContainer{@apply h-full min-h-0 w-full overflow-auto max-sm:min-h-[140px]}.scrollContainer .gridContainer{@apply grid h-full max-h-full min-w-[340px] overflow-auto pr-[10px] max-sm:min-w-[310px] max-sm:pr-0;grid-template-columns:30px minmax(0px, 1fr) minmax(0px, 1fr) 75px;column-gap:10px;grid-template-rows:50px;grid-auto-rows:60px}@media not all and (min-width: 650px){.scrollContainer .gridContainer{grid-template-rows:30px;grid-auto-rows:50px}}