.item{@apply items-center justify-items-center border-t border-[#EFEFF9];grid-column:span 6;display:grid;grid-template-columns:subgrid}.item .bodyColumn{@apply max-w-full text-[16px] text-[#6A6A6A] max-sm:text-[11px]}.item .bodyColumn .imageLimit{@apply flex h-[50px] w-[50px] justify-center overflow-hidden max-sm:h-[40px] max-sm:w-[40px]}.item .bodyColumn .imageLimit .img{@apply h-full w-full object-contain}.item:where(:global(.dark),:global(.dark) *){@apply border-[#242527]}