.wrapper{@apply h-full w-[60%] min-w-0 rounded-[20px] bg-white px-[20px] py-[15px] max-md:rounded-[6px] max-sm:px-0 max-sm:pt-0}.wrapper .scrollContainer{@apply flex h-full w-full flex-col overflow-auto pb-[10px] max-sm:overflow-hidden}.wrapper .scrollContainer .sliderHelper{transform:translateX(calc(-100% * var(--active-slide)));@apply h-full transition-transform duration-[300] ease-linear sm:translate-x-0 sm:duration-0}.wrapper .sliderControll{@apply mt-[24px] hidden px-[15px] max-sm:block}.wrapper .divider{@apply mx-[35px] hidden h-[1px] bg-[#DADAEA] max-sm:block}.wrapper .divider:where(:global(.dark),:global(.dark) *){@apply bg-[#242527]}.wrapper:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1200px){.wrapper{@apply w-full}}@media not all and (min-width: 910px){.wrapper{height:calc(100vh - 50px)}}@media not all and (min-width: 650px){.wrapper{height:calc(100vh - 40px)}}