.interactive_off{@apply flex items-center justify-center gap-[10px]}.interactive_on{@apply relative z-[2] mx-auto flex w-[100px] gap-[5px]}.interactive_on .input{@apply relative z-[3] w-full bg-transparent pl-[10px] outline-none}.interactive_on .input::placeholder{@apply text-[14px] font-normal text-[#B1B1B1]}.interactive_on .input::placeholder:where(:global(.dark),:global(.dark) *){@apply text-[#6A6A6A]}.interactive_on::after{@apply absolute -left-[5px] -top-[5px] z-[1] rounded-[6px] border border-[#D8D8D8];content:"";width:calc(100% + 10px);height:calc(100% + 10px)}.interactive_on::after:where(:global(.dark),:global(.dark) *){@apply border-[#6A6A6A]}