.container{@apply flex h-[43px] w-full items-center rounded-[10px] bg-[#FFFFFF] p-[5px] pl-[30px]}.container .input{@apply h-[43px] w-full bg-transparent text-[16px] font-normal text-input-main outline-none placeholder:text-[#B1B1B1] max-sm:h-[35px] max-sm:text-[13px]}.container .input:focus~.clearButton{@apply text-input-main}.container .input:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31] placeholder:text-[#B1B1B1]}.container .clearButton{@apply flex h-full items-center gap-[3px] pr-[12px] text-center text-input-idle}.container .clearButton .icon{@apply w-[14px] cursor-pointer opacity-60 transition-opacity ease-linear}.container .clearButton .icon:hover{@apply opacity-100}.container .clearButton:where(:global(.dark),:global(.dark) *){@apply text-input-idleDark}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}