.container{@apply rounded-[10px] bg-[#F0F5FC] px-[30px] py-[17px] max-sm:px-[20px] max-sm:pb-[5px]}.container .scrollContainer{@apply max-sm:overflow-auto max-sm:pb-[15px]}.container .scrollContainer .gridTable{@apply inline-grid w-full flex-col gap-y-[17px] max-sm:w-max;grid-template-columns:minmax(80px, 120px) minmax(140px, 1fr);grid-auto-rows:auto}@media not all and (min-width: 650px){.container .scrollContainer .gridTable{grid-template-columns:minmax(80px, 90px) minmax(140px, 1fr)}}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#252628]}.divider{@apply h-[1px] w-full bg-[#C5D5EB]}.divider:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}