.label{@apply mb-[6px] text-[13px] leading-[15px] text-input-label}.container{@apply flex h-[40px] cursor-pointer items-center rounded-[8px] border border-[#B1B1B1] max-sm:rounded-[6px]}.container .input{@apply w-full cursor-pointer bg-transparent pl-[30px] text-[15px] font-normal text-[#4674B8] outline-none placeholder:text-[#B1B1B1] max-sm:text-[14px]}.container:where(:global(.dark),:global(.dark) *){@apply border-[#6A6A6A]}.listValues{@apply absolute z-[100] max-h-[200px] w-full flex-col gap-[10px] overflow-auto rounded-[8px] border border-[#9EB9E0] bg-[#FFFFFF] px-[10px] py-[15px] max-sm:rounded-[6px];top:calc(100% + 10px)}@media not all and (min-width: 650px){.listValues{top:calc(100% + 5px)}}.listValues .value{@apply w-full cursor-pointer rounded-[5px] py-[5px] pl-[20px] text-[15px] font-normal text-[#4674B8] transition-colors ease-linear max-sm:text-[14px]}.listValues .value_active{@apply bg-[#B7D4FF]}.listValues .value_active:where(:global(.dark),:global(.dark) *){@apply bg-[#474747]}.listValues .value:not(.listValues .value_active):hover{@apply bg-[#B7D4FF]/20}.listValues .value:where(:global(.dark),:global(.dark) *):not(.listValues .value_active):hover{@apply bg-[#474747]/20}.listValues .value:where(:global(.dark),:global(.dark) *){@apply text-[#C4C4C4]}.listValues:where(:global(.dark),:global(.dark) *){@apply border-[#585E67] bg-[#585E67]}