.dynamicMarkup{@apply flex max-h-full min-h-0 w-full items-start gap-[20px]}.dynamicMarkup .switchButtonBox{@apply mt-[18px] w-[180px] flex-shrink-0}@media not all and (min-width: 1440px){.dynamicMarkup .switchButtonBox{@apply mt-0}}.dynamicMarkup .formulaMarkup{@apply flex max-h-full min-h-0 w-full grow flex-col items-center}.dynamicMarkup .formulaMarkup .rangesList{@apply mb-[25px] grid max-h-full min-h-0 w-full overflow-auto last:mb-0;grid-template-columns:minmax(0px, 150px) 15px minmax(0px, 150px) 15px minmax(0px, 150px);column-gap:20px}@media not all and (min-width: 1440px){.dynamicMarkup .formulaMarkup .rangesList{@apply px-[60px] max-md:pl-0 max-md:pr-[50px] max-sm:pr-[40px];justify-content:center}}@media not all and (min-width: 650px){.dynamicMarkup .formulaMarkup .rangesList{column-gap:10px}}.dynamicMarkup .formulaMarkup .addRangeButton{@apply h-[43px] w-[43px] shrink-0}@media not all and (min-width: 1440px){.dynamicMarkup{@apply mx-auto flex-col}}