.container{@apply flex w-full min-w-0 flex-col rounded-[20px] bg-[#FFFFFF] px-[40px] py-[30px] pr-[25px];height:calc(100% - 25px - 43px - 35px)}@media not all and (min-width: 650px){.container{height:fit-content;padding:20px 15px}}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}