.wrapper{@apply h-full w-[40%] rounded-[20px] bg-white px-[25px] py-[40px] pr-[15px] max-md:rounded-[6px]}.wrapper .gridContainer{@apply grid h-fit max-h-full overflow-auto pr-[10px];grid-template-columns:minmax(0px, 1fr) repeat(3, minmax(0px, 1.5fr));grid-template-rows:auto;grid-auto-rows:64px;column-gap:15px}.wrapper:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1200px){.wrapper{@apply w-[410px] flex-shrink-0}}@media not all and (min-width: 910px){.wrapper{@apply w-full;height:calc(100vh - 50px)}}@media not all and (min-width: 910px){.wrapper{@apply w-full;height:calc(100vh - 50px)}}@media not all and (min-width: 650px){.wrapper{height:calc(100vh - 40px)}}