.scrollContainer{@apply mb-[10px] w-full overflow-auto}.scrollContainer .gridTable{@apply grid max-sm:min-w-[560px];grid-template-columns:50px minmax(0px, 1fr) minmax(0px, 2fr) 75px}.scrollContainer .gridTable .headCell{@apply relative flex items-center gap-[5px] p-[15px] text-[16px] font-semibold text-[#6A6A6A] max-sm:text-[11px]}.scrollContainer .gridTable .hint{@apply relative}.scrollContainer .gridTable .hint .descr{@apply absolute left-[100%] top-[50%] ml-[5px] hidden w-[150px] -translate-y-1/2 rounded-md bg-[#cfedfa] p-[4px] text-[12px]}.scrollContainer .gridTable .hint .descr:where(:global(.dark),:global(.dark) *){@apply bg-[#8297B5] text-[#ffffff]}.scrollContainer .gridTable .hint:hover .descr{@apply block}.blocker{@apply absolute left-[0] top-[0] z-[30] h-full w-full cursor-not-allowed}