.container{@apply flex h-full w-[75%] flex-col rounded-[20px] bg-white p-[30px] max-sm:rounded-[6px]}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1200px){.container{@apply p-[20px]}}@media not all and (min-width: 1100px){.container{@apply min-h-[400px] w-full}}@media not all and (min-width: 650px){.container{@apply px-[15px]}}