.scrollContainer{@apply relative h-full min-h-0 w-full grow overflow-auto}.scrollContainer .gridContainer{@apply grid h-full min-w-[780px] overflow-auto pr-[15px] max-sm:min-w-[500px] max-sm:pr-0;grid-template-columns:minmax(0px, 3fr) minmax(0px, 2fr) minmax(0px, 2fr) minmax(0px, 2fr) minmax(0px, 3fr) 1fr;grid-template-rows:44px;grid-auto-rows:60px;column-gap:10px}@media not all and (min-width: 650px){.scrollContainer .gridContainer{grid-template-columns:minmax(0px, 2fr) minmax(0px, 2fr) minmax(0px, 2fr) minmax(0px, 2fr) minmax(0px, 3fr) 1fr;grid-template-rows:35px;grid-auto-rows:41px}}