.container{@apply flex h-[34px] max-w-[380px] flex-grow items-center rounded-[8px] border border-[#DBE3EF] bg-[#EFEFF9] p-[5px] pl-[30px] max-sm:w-full}.container .input{@apply h-[43px] w-full bg-transparent text-[16px] font-normal text-input-main outline-none placeholder:text-[#B1B1B1] max-sm:h-[35px] max-sm:text-[13px]}.container .input:focus~.clearButton{@apply text-input-main}.container .input:where(:global(.dark),:global(.dark) *){@apply placeholder:text-[#B1B1B1]}.container .clearButton{@apply flex h-full items-center gap-[3px] pr-[12px] text-center text-input-idle}.container .clearButton .icon{@apply w-[14px] cursor-pointer opacity-60 transition-opacity ease-linear}.container .clearButton .icon:hover{@apply opacity-100}.container .clearButton:where(:global(.dark),:global(.dark) *){@apply text-input-idleDark}.container:where(:global(.dark),:global(.dark) *){@apply border-transparent bg-[#252628]}