.page{@apply relative flex flex-col justify-between gap-[10px];height:calc(100% - 43px - 35px - 25px)}.page .flexContainer{@apply flex h-full min-h-0 justify-between gap-[20px] max-sm:flex-col}.page .flexContainer .container{@apply h-full w-[50%] min-w-0 rounded-[20px] bg-[#FFFFFF] max-sm:w-[100%] max-sm:rounded-[10px]}.page .flexContainer .container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 650px){.page{height:100%}}