.container{@apply flex w-[70%] flex-col gap-[17px] rounded-[20px] bg-white p-[30px] max-sm:rounded-[6px]}.container .divider{@apply h-[1px] w-full shrink-0 bg-[#EFEFF9]}.container .divider:where(:global(.dark),:global(.dark) *){@apply bg-[#242527]}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1440px){.container{@apply p-[20px] max-sm:px-[15px]}}@media not all and (min-width: 1100px){.container{@apply w-full}}