.container{@apply flex h-full min-h-0 w-full flex-col overflow-hidden rounded-[20px] bg-white max-sm:rounded-[6px]}@media not all and (min-width: 1024px){.container{height:calc(100vh - 50px)}}@media not all and (min-width: 650px){.container{height:calc(100vh - 30px)}}.container .title{@apply flex max-sm:bg-[#F0F5FC]}.container .title .label{@apply w-fit bg-[#B7D4FF] px-[30px] py-[10px] text-[15px] font-medium text-[#4674B8] max-sm:mx-auto max-sm:bg-[#F0F5FC] max-sm:px-[40px] max-sm:py-[8px] max-sm:text-[14px];border-radius:20px 0px}@media not all and (min-width: 650px){.container .title .label{border-radius:0px 0px 6px 6px}}.container .title .label:where(:global(.dark),:global(.dark) *){@apply bg-[#585E67] text-[#C5C5C5] max-sm:bg-[#252628] max-sm:text-[#4674B8]}.container .title:where(:global(.dark),:global(.dark) *){@apply max-sm:bg-[#252628]}.container .content{@apply h-full min-h-0 py-[40px] pl-[30px] pr-[10px] max-sm:pl-[15px] max-sm:pr-[5px]}.container .content .gridContainer{@apply relative grid h-full overflow-auto pr-[15px] max-sm:pr-[10px];grid-template-columns:repeat(3, 1fr);grid-template-rows:45px;grid-auto-rows:70px}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}