.wrapper{@apply grid h-full max-h-full min-h-0 overflow-auto pr-[10px] max-sm:min-h-[140px] max-sm:pr-0;grid-template-columns:50px minmax(0px, 1fr) 70px;grid-template-rows:50px;grid-auto-rows:60px;column-gap:10px}@media not all and (min-width: 770px){.wrapper{grid-template-columns:40px minmax(0px, 1fr) 70px}}@media not all and (min-width: 650px){.wrapper{grid-template-columns:40px minmax(0px, 1fr) 50px;grid-template-rows:30px;grid-auto-rows:50px}}