.gridContainer{width:100%;display:grid;grid-template-columns:repeat(var(--col-count), 1fr);grid-gap:8px;grid-auto-rows:140px;justify-items:center}@media(max-width: 1400px){.gridContainer{grid-template-columns:repeat(calc(var(--col-count) - 1), 1fr)}}@media not all and (min-width: 910px){.gridContainer{grid-template-columns:repeat(calc(var(--col-count) + 1), 1fr)}}@media not all and (min-width: 820px){.gridContainer{grid-template-columns:repeat(calc(var(--col-count)), 1fr)}}@media not all and (min-width: 700px){.gridContainer{grid-template-columns:repeat(calc(var(--col-count) - 1), 1fr)}}