.range{@apply relative;grid-column:span 5;display:grid;grid-template-columns:subgrid}.range .autoFromPrice{@apply mt-[25.5px] flex h-[35px] w-full items-center rounded-[6px] bg-[#EEEEF8] px-[20px] text-[15px] font-medium text-[#4674B8] outline-none max-sm:h-[29px] max-sm:px-[15px]}.range .autoFromPrice:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B]}.range .specSymbol{@apply mt-[23px] text-[24px] text-[#6A6A6A]}.range .infinity{@apply mt-[23px] flex h-[36px] items-center justify-center}.errorMessage{@apply mb-[25px] mt-[5px] text-[13px] font-normal leading-[15px] text-input-failure last:mb-0 max-sm:mb-[15px];grid-column:1/-1}