.layout{@apply min-h-[100vh] w-full p-[25px] pl-0 max-sm:px-[10px] max-sm:pb-[20px] max-sm:pt-0}@media not all and (min-width: 650px){.layout{min-height:calc(100vh - 75px)}}.layout .container{@apply grid h-full w-full gap-x-[20px];grid-template-columns:320px minmax(0px, 1fr);grid-template-rows:auto minmax(0px, calc(100vh - 43px - 35px - 25px - 45px - 5px));grid-template-areas:"currentPage topPanel" "menuGrops tableProducts"}@media not all and (min-width: 1024px){.layout .container{grid-template-columns:260px minmax(0px, 1fr)}}@media not all and (min-width: 770px){.layout .container{grid-template-areas:"currentPage topPanel" "menuGrops menuGrops" "tableProducts tableProducts";grid-template-columns:180px minmax(0px, 1fr);grid-template-rows:auto auto minmax(300px, 1fr)}}@media(orientation: portrait){.layout .container{grid-template-areas:"currentPage topPanel" "menuGrops menuGrops" "tableProducts tableProducts";grid-template-columns:180px minmax(0px, 1fr);grid-template-rows:auto auto minmax(300px, 1fr)}}@media not all and (min-width: 650px){.layout .container{grid-template-areas:"topPanel" "menuGrops" "tableProducts";grid-template-columns:minmax(0px, 1fr);grid-template-rows:auto auto minmax(300px, 1fr)}}.layout .container .currentPage{@apply mb-[35px] ml-[25px] mt-[25px] flex items-center gap-[20px] text-[36px] font-medium leading-[42px] text-[#171717] max-md:ml-0 max-sm:hidden;grid-area:currentPage}.layout .container .currentPage:where(:global(.dark),:global(.dark) *){@apply text-[#F0F5FC]}.layout .container .tableBox{@apply relative flex flex-col justify-between rounded-[20px] bg-[#FFFFFF] px-[30px] py-[30px] pr-[15px] max-md:rounded-[6px] max-sm:px-[15px] max-sm:py-[20px] portrait:rounded-[6px] portrait:px-[15px] portrait:py-[20px];grid-area:tableProducts}.layout .container .tableBox:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}