.container{@apply mb-[20px] flex w-full gap-[40px] rounded-[20px] bg-white px-[60px] py-[30px] pb-[20px] pt-[25px] max-md:rounded-[6px] max-sm:justify-around max-sm:px-[20px]}.container .image{@apply flex max-w-[150px] items-center justify-center rounded-[10px]}.container .image img{@apply w-full object-contain}.container .box{@apply flex flex-col justify-between py-[15px]}.container .box .name{@apply text-[20px] font-medium text-[#171717]}.container .box .name:where(:global(.dark),:global(.dark) *){@apply text-[#EFEFF9]}.container .box .providers{@apply flex gap-[10px]}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 910px){.container{@apply mb-[25px]}}