.topPanel{@apply mb-[25px] mr-[20px] mt-[15px] flex items-center justify-between max-sm:mb-[20px] max-sm:mr-0 max-sm:mt-0}.topPanel .container{@apply ml-[25px] flex items-center gap-[20px] max-sm:ml-0}.topPanel .container .currentPage{@apply text-[36px] font-medium leading-[42px] text-[#171717] max-sm:hidden;grid-area:currentPage}.topPanel .container .currentPage:where(:global(.dark),:global(.dark) *){@apply text-[#F0F5FC]}@media not all and (min-width: 820px){.topPanel .container .currentPage{@apply text-[26px]}}@media not all and (min-width: 700px){.topPanel .container .currentPage{@apply text-[23px]}}@media not all and (min-width: 700px){.topPanel .container{@apply gap-[5px]}}.topPanel .button{@apply h-[43px] w-[230px] max-sm:h-[35px] max-sm:w-[174px]}