.container{@apply mb-[30px] flex justify-between gap-[10px]}.container .mobileButton{@apply hidden gap-[10px]}.container .mobileButton .categoryButton{@apply flex h-[43px] w-[43px] cursor-pointer items-center justify-center rounded-[6px] bg-[#C2DBFF]}.container .mobileButton .searchButton{@apply flex h-[43px] w-[43px] cursor-pointer items-center justify-center rounded-[6px] bg-[#8297B5]}.container .mobileButton .mobileSearch{@apply absolute top-0 z-20 hidden w-[200px] rounded-[6px] border border-[#EEEEF8] bg-white;left:calc(100% + 10px);filter:drop-shadow(0px 0px 25px rgba(70, 116, 184, 0.2))}.container .mobileButton .mobileSearch:where(:global(.dark),:global(.dark) *){@apply border-[#6A6A6A] bg-[#2c2d31];filter:drop-shadow(0px 0px 25px rgba(0, 0, 0, 0.2))}@media not all and (min-width: 1100px){.container .mobileButton{@apply flex}}.container .searchBox{@apply w-[320px] rounded-[8px] border border-[#EEEEF8]}.container .searchBox:where(:global(.dark),:global(.dark) *){@apply border-[#6A6A6A]}@media not all and (min-width: 1100px){.container .searchBox{@apply hidden}}.container .customButton{@apply h-[43px] w-[250px]}.container .customButton span{@apply text-[13px] leading-[13px]}@media not all and (min-width: 1200px){.container{@apply mb-[20px]}}