.container{@apply h-full w-[25%] min-w-0 rounded-[20px] bg-white px-[30px] py-[35px] pt-[15px] max-sm:rounded-[10px]}.container .scrollContainer{@apply w-full overflow-auto px-[2px]}@media not all and (min-width: 1100px){.container .scrollContainer{@apply h-full pr-[10px]}}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1200px){.container{@apply px-[20px]}}@media not all and (min-width: 1100px){.container{@apply absolute z-10 hidden w-full;padding:30px 10px 30px 30px;height:calc(100% - 38px - 40px);top:78px;filter:drop-shadow(0px 0px 25px rgba(70, 116, 184, 0.2))}.container:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B];filter:drop-shadow(0px 0px 25px rgba(0, 0, 0, 0.2))}}.container::after{content:"";@apply absolute -top-[8px] left-[20px] z-[9] hidden h-[20px] w-[20px] rounded-[5px] bg-white;transform:rotate(-45deg);filter:drop-shadow(0px 0px 25px rgba(70, 116, 184, 0.1))}.container::after:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B];filter:drop-shadow(0px 0px 25px rgba(54, 55, 59, 0.2))}@media not all and (min-width: 1100px){.container::after{@apply block}}