.scrollContainer{@apply h-full min-h-0 w-full grow overflow-auto}.scrollContainer .gridContainer{@apply grid h-full min-w-[790px] overflow-auto pr-[15px] max-sm:min-w-[600px] max-sm:pr-0;grid-template-columns:1fr minmax(0px, 1fr) minmax(0px, 3fr) minmax(0px, 2fr) minmax(0px, 2fr) 1fr 1fr;grid-template-rows:40px;grid-auto-rows:57px;column-gap:10px}@media not all and (min-width: 650px){.scrollContainer .gridContainer{grid-template-columns:1fr minmax(0px, 2fr) minmax(0px, 4fr) minmax(0px, 3fr) minmax(0px, 3fr) 2fr 2fr;grid-template-rows:35px;grid-auto-rows:41px}}