.row{@apply rounded-[8px] px-[15px] transition-colors ease-linear;grid-column:span 4;display:grid;grid-template-columns:subgrid}.row .cell{@apply flex w-full items-center border-t border-t-[#E5E5EB] p-[15px] text-[16px] text-[#6A6A6A] transition-colors ease-linear max-sm:text-[10px]}.row .cell:where(:global(.dark),:global(.dark) *){@apply border-t-[#242527]}.row .mainStar{@apply relative}.row .mainStar .icon{@apply h-[16px] w-[16px] fill-[#4674B8]}.row .mainStar .descr{@apply absolute left-[100%] top-[50%] ml-[5px] hidden w-[130px] -translate-y-1/2 rounded-md bg-[#cfedfa] p-[4px] text-center text-[12px]}.row .mainStar .descr:where(:global(.dark),:global(.dark) *){@apply bg-[#8297B5] text-[#ffffff]}.row .mainStar:hover .descr{@apply block}.row .sku{@apply w-fit max-w-full truncate}.row .listCombinations{@apply flex w-full flex-wrap gap-[5px]}.row_active{@apply border-l-[3px] border-l-[#4674B8]}.row:not(.row_active){@apply pl-[18px]}.row:hover{@apply bg-[#F0F5FC]}.row:hover:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B]}.blocker{@apply absolute left-[0] top-[0] z-[30] h-full w-full cursor-not-allowed}