.topPanel{@apply mb-[35px] mt-[25px] flex items-center justify-between gap-[10px] max-sm:mb-[20px] max-sm:mt-0;grid-area:topPanel}.topPanel .search{@apply max-w-[505px] grow}.topPanel .button{@apply h-[43px] w-[244px] max-sm:h-[40px] max-sm:w-[174px]}.topPanel .button .custom{@apply rounded-[10px] px-[15px] max-lg:min-w-[190px] max-lg:gap-[10px] max-md:min-w-[170px] max-sm:rounded-[6px]}.topPanel .button .custom span{@apply max-lg:text-[14px] max-md:text-[12px]}.topPanel .button .custom span svg{@apply max-md:w-[10px]}