.layout{@apply min-h-[100vh] w-full p-[25px] pl-0 max-sm:px-[10px] max-sm:pb-[20px] max-sm:pt-0}@media not all and (min-width: 650px){.layout{height:calc(100% - 75px)}}.layout .container{@apply grid h-full w-full gap-x-[20px];grid-template-columns:320px minmax(0px, 1fr);grid-template-rows:auto minmax(0px, calc(100vh - 43px - 35px - 25px - 45px - 5px));grid-template-areas:"currentPage topPanel" "menuGrops books"}.layout .container .page{@apply relative flex flex-col gap-[10px] rounded-[20px] bg-[#FFFFFF] p-[30px] pr-[15px] max-md:rounded-[6px] max-sm:py-[20px] max-sm:pl-[15px] portrait:rounded-[6px];grid-area:books}.layout .container .page:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 1024px){.layout .container{grid-template-columns:260px minmax(0px, 1fr)}}@media not all and (min-width: 770px){.layout .container{grid-template-areas:"currentPage topPanel" "menuGrops menuGrops" "books books";grid-template-columns:180px minmax(0px, 1fr);grid-template-rows:auto auto minmax(300px, 1fr)}}@media(orientation: portrait){.layout .container{grid-template-areas:"currentPage topPanel" "menuGrops menuGrops" "books books";grid-template-columns:180px minmax(0px, 1fr);grid-template-rows:auto auto minmax(300px, 1fr)}}@media not all and (min-width: 650px){.layout .container{grid-template-areas:"topPanel" "menuGrops" "books";grid-template-columns:minmax(0px, 1fr);grid-template-rows:auto}}