.scrollContainer{@apply mb-[30px] h-full min-h-0 w-full grow overflow-auto}.scrollContainer .gridContainer{@apply grid h-full min-w-[890px] overflow-auto pr-[15px] max-sm:min-w-[600px];grid-template-columns:max-content minmax(0px, 2fr) minmax(0px, 1fr) minmax(0px, 3fr) minmax(100px, 1fr);grid-template-rows:40px;grid-auto-rows:auto;column-gap:40px}