.gridRow{@apply items-center gap-x-[10px] border-b border-[#EFEFF9] px-[38px] py-[18px] max-sm:px-0;grid-column:span 3;display:grid;grid-template-columns:subgrid}.gridRow .dateCell{@apply text-[15px] text-[#6A6A6A] max-sm:text-[12px]}.gridRow .balanceUp{@apply h-fit w-fit rounded-[5px] bg-[#D8FFD1] px-[20px] py-[6px] text-[14px] text-[#3EAA2D] max-sm:px-[10px] max-sm:text-[10px]}.gridRow .balanceUp:where(:global(.dark),:global(.dark) *){@apply bg-[#274821] text-[#3EAA2D]}.gridRow .amountUp{@apply text-[15px] text-[#3EAA2D] max-sm:text-[12px]}.gridRow .balanceDown{@apply h-fit w-fit rounded-[5px] bg-[#FFD8D8] px-[20px] py-[6px] text-[14px] text-[#D03A3A] max-sm:px-[10px] max-sm:text-[10px]}.gridRow .balanceDown:where(:global(.dark),:global(.dark) *){@apply bg-[#6A2323] text-[#F43B3B]}.gridRow .amountDown{@apply text-[15px] text-[#F43B3B] max-sm:text-[12px]}.gridRow:where(:global(.dark),:global(.dark) *){@apply border-[#242527]}