.item{@apply flex h-full w-[280px] flex-grow flex-col rounded-[20px] bg-white max-md:rounded-[6px] max-sm:w-full max-sm:flex-shrink-0;box-shadow:0px 0px 13px 0px rgba(183,212,255,.3)}.item:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31];box-shadow:0px 0px 13px 0px rgba(0,0,0,.3)}@media not all and (min-width: 650px){.item{box-shadow:none}}