.item{@apply flex min-w-0 shrink-0 gap-[10px] max-xl:flex-wrap max-sm:rounded-[10px] max-sm:border max-sm:border-[#DEDEDE] max-sm:p-[20px]}.item .name{@apply flex h-[40px] w-[100%] min-w-0 items-center rounded-[8px] bg-[#EEEEF8] px-[30px] text-[15px] font-medium leading-[18px] text-[#4674B8]}@media not all and (min-width: 650px){.item .name{width:calc(100% - 90px - 40px - 15px - 10px)}}.item .name:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B] text-[#8A8A8A]}.item .fileFormat{@apply flex h-[40px] w-[90px] shrink-0 items-center justify-center rounded-[8px] border border-[#4674b8] text-[#4674B8] max-sm:mr-[5px]}.item .dotsButton{@apply relative h-[40px] w-[40px] shrink-0 max-sm:order-3}.item:where(:global(.dark),:global(.dark) *){@apply max-sm:border-[#6A6A6A]}